<template>
  <!-- 文章详情页面 -->
  <div class="details">
    <!-- 后退按键 -->
    <p class="back" title="后退" @click="$router.go(-1)">
      <span class="iconfont icon-31fanhui1"></span>
    </p>
    <!-- 自定义滚动条 -->
    <ScrollContainer >
        <!-- md文章整体内容 -->
        <div class="markdown-body">
          <p>
            <!-- 笔记时间 -->
            <span class="date_text">笔记时间：{{ date }}</span>
            <!-- 笔记标签 -->
            <span class="tag_text">
              <span class="tagItem" v-for="(tag, index) in tags" :key="index">{{
                tag
              }}</span>
            </span>
          </p>
          <!-- md文件组件 -->
          <component :is="parameter"></component>
          <!-- 文章底部信息 -->
          <p>SubTopH 笔记 @交流微信 h595191530</p>
        </div>
    </ScrollContainer>
  </div>
</template>
<script>
import { onMounted, ref, nextTick,defineComponent} from "vue";
import { useRoute } from "vue-router";
export default defineComponent({
  setup() {
    const scrollbarRef = ref(null);
    const route = useRoute();
    const parameter = ref(null);
    const date = ref(null);
    const tags = ref(null);
    onMounted(() => {
      getParameters();
    });
    // 获取路径中文章信息
    const getParameters = function () {
      // 获取文章参数  点击跳转获取参数  或者 刷新页面从url中获取参数
      parameter.value = route.query.article || location.search.split("=")[1];
      // 获取文章记录时间
      date.value = route.query.date || location.search.split("=")[2];
      // 获取文章标签
      tags.value = route.query.tag || location.search.split("=")[3];
      // 判断标签是不是字符串 是字符串就转成数据
      if (typeof tags.value == "string") {
        tags.value = [tags.value];
      }
    };
    return {
      scrollbarRef,
      parameter,
      date,
      tags,
    };
  },
});
</script>
<style lang="less" scoped>
.details {
  height: 100%;
  width: 80%;
  margin: 0 auto;
  .back {
    position: absolute;
    background: #fff;
    top: 10px;
    left: 10px;
    height: 30px;
    margin: 0;
    line-height: 30px;
    text-align: center;
    width: 30px;
    box-shadow: 1px 1px 2px 1px #aaa;
    border-radius: 10px;
    cursor: pointer;
    transition: 0.3s;
    z-index: 555;
    &:hover {
      color: #fff;
      background: rgb(0, 60, 255);
    }
  }
  .markdown-body {
    padding: 40px;
    height: 100%;
    p {
      background: rgb(243, 243, 243);
      border-radius: 3px;
      overflow: hidden;
      color: rgb(105, 105, 105);
      padding: 0 10px;
      font-size: 14px;
      line-height: 30px;
      .date_text {
        float: left;
      }
      .tag_text {
        float: right;
        .tagItem {
          background: rgb(66, 66, 66);
          padding: 1px 10px;
          margin: 0 3px;
          color: #fff;
          border-radius: 5px;
        }
      }
    }
  }
}

.min .markdown-body {
  margin: 0;
  font-size: 10px;
  padding: 40px 0px;
}

.min .date_text,
.min .tagItem {
  font-size: 12px;
}
.med .details{
  width: 100%;
}
</style>
